@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;500;600&family=Roboto:ital,wght@0,300;0,400;0,500;0,700;0,900;1,700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap');

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

.font-fam-roboto {
  font-family: 'Roboto', sans-serif;
}

.font-fam-poppins {
  font-family: 'Poppins', sans-serif;
}

.font-fam-fjalla {
  font-family: 'Fjalla One', sans-serif;
}

.font-300 {
  font-weight: 300;
}

.font-400 {
  font-weight: 400;
}

.font-500 {
  font-weight: 500;
}

.font-600 {
  font-weight: 600;
}

.font-700 {
  font-weight: 700;
}

.font-900 {
  font-weight: 900;
}

.bg-default {
  /* background-color: #2d3748; */
  background-color: #910000de;
}

.active {
  border: 3px solid white;
}

.bs-1 {
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
}

.topbar {
  position: absolute;
  right: 8px;
}

.d-none {
  display: none;
}

.bg-set{
  background: #ecf0f1;
}